#launchContainer {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
}

#launchContainer > div {
  text-align: center;
}

h1, h2 {
  color: chocolate;
}

#titan-caption {
  position: relative;
  display: table;
  height: 100%;
  width: 70%;
  margin: 0 auto;
}

#titan-caption {
  position: relative;
  display: table;
  height: 100%;
  width: 100%;
  margin: 0 auto;
}

#caption-content {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

#font-alt {
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 30px !important;
  letter-spacing: 4px;
  font-size: 15px;
  color: chocolate;
}

#font-alt-two {
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 40px !important;
  letter-spacing: 24px;
  font-weight: 400;
  font-size: 48px;
  color: chocolate;
}

#bg-dark-30 {
  color: #fff;
  background-color: rgba(2, 2, 2, 0.7);
  position: relative;
  padding: 140px 0;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  background-attachment: fixed;
  background-size: cover;
}

#btn-loo {
  transition: all 0.125s ease-in-out 0s;
  background: transparent;
  border: 1px solid chocolate;
  color: chocolate;
  border-radius: 2px;
}

#btn-loo:hover,
#btn-loo:focus {
  background: chocolate;
  border-color: transparent;
  color: #111;
}
